import * as vscode from "vscode";
import { ContextTypeData, DiagnosticItem } from "./enrichingContextTypes";

export default async function getDiagnosticsContext(
  editor: vscode.TextEditor
): Promise<ContextTypeData | undefined> {
  const diagnosticsContext = buildDiagnosticsContext(editor);
  if (!diagnosticsContext) return undefined;

  return Promise.resolve({
    type: "Diagnostics",
    diagnostics: diagnosticsContext,
  });
}

function getDiagnosticsWithPredicate(
  editor: vscode.TextEditor,
  predicate: (e: vscode.Diagnostic) => boolean
): DiagnosticItem[] {
  const selectedRange = editor.selection;

  return vscode.languages
    .getDiagnostics(editor.document.uri)
    .filter((e: vscode.Diagnostic) => {
      if (!predicate(e)) return false;
      if (selectedRange.isEmpty) {
        return editor.visibleRanges.some((r) => r.contains(e.range));
      }
      return selectedRange.contains(e.range);
    })
    .map((e: vscode.Diagnostic) => {
      const relatedInfo: string | undefined = e.relatedInformation
        ?.map((info) => info.message)
        .reduce((info, message) => `${info}\n${message}`, "");
      const errorMessage = relatedInfo ? e.message + relatedInfo : e.message;
      const item: DiagnosticItem = {
        errorMessage: e.source ? `${e.source}: ${errorMessage}` : errorMessage,
        lineCode: editor.document.lineAt(e.range.start.line).text.trim(),
        lineNumber: e.range.start.line + 1,
        level: vscode.DiagnosticSeverity[e.severity],
      };
      return item;
    });
}

// The function prioritizes handling errors before addressing any warnings.
export function buildDiagnosticsContext(
  editor: vscode.TextEditor
): DiagnosticItem[] {
  const errors = getDiagnosticsWithPredicate(
    editor,
    (e: vscode.Diagnostic) => vscode.DiagnosticSeverity.Error === e.severity
  );

  if (errors.length > 0) {
    return errors;
  }
  return getDiagnosticsWithPredicate(
    editor,
    (e: vscode.Diagnostic) => vscode.DiagnosticSeverity.Warning === e.severity
  );
}
