import {
  getSymbolTextCommandExecutor,
  SymbolText,
  SymbolTextRequest,
} from "../workspaceCommands/getSymbolsText";

export interface GetSymbolsTextRequest {
  symbols: SymbolTextRequest[];
}
export interface GetSymbolsTextResponse {
  symbols: SymbolText[];
}

/**
 * Get the text of the symbols
 * @param symbols - the symbols to get the text of
 * @returns the text of the symbols
 */
export async function getSymbolsText({
  symbols,
}: GetSymbolsTextRequest): Promise<GetSymbolsTextResponse> {
  return { symbols: await getSymbolTextCommandExecutor(symbols) };
}
