import * as vscode from "vscode";
import * as path from "path";

export type SymbolInformationResult = vscode.SymbolInformation & {
  relativePath: string;
};

export async function resolveWorkspaceSymbols({
  symbol,
}: {
  symbol: string;
}): Promise<SymbolInformationResult[] | undefined> {
  const symbols = await vscode.commands.executeCommand<
    vscode.SymbolInformation[]
  >("vscode.executeWorkspaceSymbolProvider", symbol);

  if (!symbols) return undefined;

  const workspacePath = vscode.workspace.workspaceFolders?.[0].uri.fsPath;

  if (!workspacePath) return [];

  const normalizedWorkspacePath = path.normalize(workspacePath);

  return symbols
    .map((workspaceSymbol) => {
      const normalizedSymbolPath = path.normalize(
        workspaceSymbol.location.uri.fsPath
      );

      const relativePath = normalizedSymbolPath.startsWith(
        normalizedWorkspacePath
      )
        ? path.relative(normalizedWorkspacePath, normalizedSymbolPath)
        : undefined;

      if (!relativePath || isProbablyNotSource(relativePath)) {
        return undefined;
      }
      return {
        ...workspaceSymbol,
        relativePath,
      };
    })
    .filter(
      (workspaceSymbol) => !!workspaceSymbol
    ) as SymbolInformationResult[];
}

function isProbablyNotSource(symbolPath: string): boolean {
  return ["node_modules", "dist", "build", "target", "out"].some((dir) =>
    path.normalize(symbolPath).startsWith(dir)
  );
}
