import * as vscode from "vscode";

export type InserCodeAtLine = {
  code: string;
  lineNumber: number;
};

export async function insertAtLine({
  code,
  lineNumber,
}: InserCodeAtLine): Promise<void> {
  const editor = vscode.window.activeTextEditor;

  if (!editor) {
    void vscode.window.showErrorMessage("No active text editor found.");
    return;
  }

  const startPosition = new vscode.Position(lineNumber, 0);

  await editor.edit((editBuilder) => {
    editBuilder.insert(startPosition, `${code}\n`);
  });
  await editor.document.save();

  const endPosition = startPosition.translate(
    1 + code.split("\n").length - 1,
    0
  );
  editor.selection = new vscode.Selection(startPosition, endPosition);
  editor.revealRange(
    new vscode.Range(startPosition, endPosition),
    vscode.TextEditorRevealType.Default
  );
}
