import { SymbolKind } from "vscode";
import { SymbolInformationResult } from "../handlers/resolveSymbols";

export type WorkspaceSymbolKind =
  | "Method"
  | "Function"
  | "Class"
  | "File"
  | "Other";

export type WorkspaceSymbol = {
  name: string;
  kind: WorkspaceSymbolKind;
  relativePath: string;
  absolutePath: string;
  range: {
    startLine: number;
    startCharacter: number;
    endLine: number;
    endCharacter: number;
  };
};

/**
 * Converts a SymbolKind value to a WorkspaceSymbolKind value.
 * @param kind - The SymbolKind value to convert.
 * @returns The WorkspaceSymbolKind value.
 */
export function toChatSymbolKind(kind: SymbolKind): WorkspaceSymbolKind {
  switch (kind) {
    case SymbolKind.Method:
      return "Method";
    case SymbolKind.Function:
      return "Function";
    case SymbolKind.Class:
      return "Class";
    case SymbolKind.File:
      return "File";
    default:
      return "Other";
  }
}

export function toWorkspaceSymbol(symbol: SymbolInformationResult) {
  return {
    name: symbol.name,
    relativePath: symbol.relativePath,
    absolutePath: symbol.location.uri.fsPath,
    kind: toChatSymbolKind(symbol.kind),
    range: {
      startLine: symbol.location.range.start.line,
      endLine: symbol.location.range.end.line,
      startCharacter: symbol.location.range.start.character,
      endCharacter: symbol.location.range.end.character,
    },
  };
}
