import * as vscode from "vscode";
import { WorkspaceSymbolKind } from "./symbols";

export type SymbolTextRequest = {
  id: string;
  absolutePath: string;
  kind: WorkspaceSymbolKind;
  range: {
    startLine: number;
    startCharacter: number;
    endLine: number;
    endCharacter: number;
  };
};

export type SymbolText = {
  id: string;
  snippet: string;
};

/**
 * Returns a list of text snippets for selected ranges.
 *
 * @returns {Promise<SymbolText[]>} - An array of related snippets per ID.
 * @param workspaceSymbols
 */
export async function getSymbolTextCommandExecutor(
  workspaceSymbols: SymbolTextRequest[]
): Promise<SymbolText[]> {
  const promises: Promise<{
    id: string;
    snippet: string;
  }>[] = workspaceSymbols.map(async (symbol) => {
    const document = await vscode.workspace.openTextDocument(
      symbol.absolutePath
    );
    if (symbol.kind === "File") {
      return {
        id: symbol.id,
        snippet: document.getText(),
      };
    }
    const snippet = document.getText(
      new vscode.Range(
        symbol.range.startLine,
        symbol.range.startCharacter,
        symbol.range.endLine,
        symbol.range.endCharacter
      )
    );
    return {
      id: symbol.id,
      snippet,
    };
  });
  return Promise.all(promises);
}
