import * as vscode from "vscode";
import { WorkspaceSymbol } from "../workspaceCommands/symbols";
import { getSymbolsCommandExecutor } from "../workspaceCommands/getSymbols";

export interface GetSymbolsRequest {
  query: string;
  maxNumOfResults: number;
}

/**
 * An object containing the list of document symbols and workspace symbols.
 */
export type GetSymbolsResponse = {
  /**
   * The list of document symbols.
   */
  documentSymbols: WorkspaceSymbol[];
  /**
   * The list of workspace symbols.
   */
  workspaceSymbols: WorkspaceSymbol[];
};

const dummyFileUri = vscode.Uri.parse("file://");

/**
 * Returns a list of symbols in the current project based on the given search argument.
 * @param {string} query - The search argument.
 * @param {number} maxNumOfResults - The maximum number of results to return.
 * @returns {GetSymbolsResponse} - A list of symbols in the current project.
 */
export async function getSymbols({
  query,
  maxNumOfResults,
}: GetSymbolsRequest): Promise<GetSymbolsResponse> {
  const fileUri = vscode.window.activeTextEditor?.document.uri ?? dummyFileUri;
  return getSymbolsCommandExecutor(query, maxNumOfResults, fileUri);
}
