import * as vscode from "vscode";

export type BrowseFolderResponse = {
  path: string;
};

export async function browseFolder(): Promise<BrowseFolderResponse> {
  const selectedFolderUri = await vscode.window.showOpenDialog({
    canSelectMany: false,
    openLabel: "Select Folder",
    canSelectFiles: false,
    canSelectFolders: true,
  });

  if (selectedFolderUri && selectedFolderUri.length > 0) {
    return {
      path: selectedFolderUri[0].fsPath,
    };
  }

  throw new Error("No folder selected");
}
