import fetch, { Response, RequestInit, RequestInfo } from "node-fetch";
import { URL } from "url";

interface RequestInitTimeout extends RequestInit {
  timeout?: number;
}

export interface DoFetchRequest {
  input: RequestInfo | URL;
  initWithTimeout?: RequestInitTimeout;
}

export type DoFetchResponse =
  | {
      status: number;
      statusText: string;
      headers: Record<string, string>;
      body: string;
      ok: boolean;
    }
  | { error: Error };

export async function doFetch({
  input,
  initWithTimeout,
}: DoFetchRequest): Promise<DoFetchResponse> {
  try {
    const response: Response = await fetch(input, initWithTimeout);

    const textResponse: string = await response.text();
    const headers: Record<string, string> = {};
    response.headers.forEach((value, key) => {
      headers[key] = value;
    });

    return {
      status: response.status,
      statusText: response.statusText,
      headers,
      body: textResponse,
      ok: response.ok,
    };
  } catch (error) {
    const isError = error instanceof Error;
    const safeError: Error = isError
      ? (error as Error)
      : new Error(String(error));
    return { error: safeError };
  }
}
