import EventEmitterBasedState from "../state/EventEmitterBasedState";
import {
  Capability,
  isCapabilitiesReady,
  isCapabilityEnabled,
  onDidRefreshCapabilities,
} from "./capabilities";

export default class CapabilityState extends EventEmitterBasedState<boolean> {
  constructor(capability: Capability) {
    super(isCapabilitiesReady() ? isCapabilityEnabled(capability) : null);

    onDidRefreshCapabilities(() => {
      if (isCapabilitiesReady()) {
        this.set(isCapabilityEnabled(capability));
      }
    });
  }
}
