import { Response } from "node-fetch";
import {
  EventSourceMessage,
  fetchEventSource,
} from "@ai-zen/node-fetch-event-source";

export interface DoStreamingFetchRequest {
  url: string;
  method: "POST";
  headers: Record<string, string>;
  body: string | ArrayBufferView | ArrayBuffer;
  streamingId: string;
}

export interface DoStreamingFetchResponse {
  status: number;
  statusText: string;
  headers: Record<string, string>;
  ok: boolean;
}

export type DoStreamingFetchStreamingPayload = {
  id: string;
  event: "open" | "message" | "error" | "close";
  data: string;
};

const activeStreams = new Map<string, AbortController>();

export function abortStreamingFetch({ streamingId }: { streamingId: string }) {
  const abortController = activeStreams.get(streamingId);

  if (abortController) {
    abortController.abort();
    activeStreams.delete(streamingId);
  }
}

export async function doStreamingFetch(
  { url, method, headers, body, streamingId }: DoStreamingFetchRequest,
  postMessage: ((message: unknown) => Thenable<boolean>) | undefined
): Promise<void> {
  let streamingMessagesCounter = 0;

  const abortController = new AbortController();
  activeStreams.set(streamingId, abortController);

  const handlePostMessage = (payload: DoStreamingFetchStreamingPayload) => {
    void postMessage?.({
      id: `${streamingId}_${streamingMessagesCounter}`,
      payload,
      type: "streaming",
      streamingId,
    });
  };

  const onOpenHandler = (response: Response) => {
    // beware, do not read the response body here, otherwise it will error in onmessage handler
    const responseHeaders: Record<string, string> = {};
    response.headers.forEach((value, key) => {
      responseHeaders[key] = value;
    });
    const serializableResponse = {
      status: response.status,
      statusText: response.statusText,
      headers: responseHeaders,
      ok: response.ok,
    };

    handlePostMessage({
      event: "open",
      data: JSON.stringify(serializableResponse),
      id: streamingId,
    });
  };

  const onMessageHandler = (msg: EventSourceMessage) => {
    streamingMessagesCounter += 1;

    handlePostMessage({
      event: "message",
      data: msg.data,
      id: streamingId,
    });
  };

  const onErrorHandler = (error: Error) => {
    console.error(error);
    // throwing an error to stop the retry
    throw error;
  };

  const onCloseHandler = () => {
    handlePostMessage({
      event: "close",
      data: "",
      id: streamingId,
    });
  };

  return fetchEventSource(url, {
    method,
    headers,
    body,
    signal: abortController.signal,
    // @ts-expect-error check response mismatch...
    onopen: onOpenHandler,
    onmessage: onMessageHandler,
    /**
     * Called when there is any error making the request / processing messages /
     * handling callbacks etc. Use this to control the retry strategy: if the
     * error is fatal, rethrow the error inside the callback to stop the entire
     * operation. Otherwise, you can return an interval (in milliseconds) after
     * which the request will automatically retry (with the last-event-id).
     * If this callback is not specified, or it returns undefined, fetchEventSource
     * will treat every error as retriable and will try again after 1 second.
     */
    onerror: onErrorHandler,
    onclose: onCloseHandler,
  }).catch((error: Error) => {
    console.error(error);
    handlePostMessage({
      event: "error",
      data: JSON.stringify(error.message),
      id: streamingId,
    });
  });
}
