import * as vscode from "vscode";

export type BrowseFileResponse = {
  code: string;
  path: string;
};

export async function browseFile(): Promise<BrowseFileResponse> {
  await vscode.commands.executeCommand("workbench.action.quickOpen");

  const selectedFileUri = await new Promise<vscode.Uri | undefined>(
    (resolve) => {
      const disposable = vscode.window.onDidChangeActiveTextEditor((editor) => {
        if (editor) {
          disposable.dispose();
          resolve(editor.document.uri);
        }
      });
    }
  );

  if (selectedFileUri) {
    const document = await vscode.workspace.openTextDocument(selectedFileUri);
    return {
      code: document.getText(),
      path: document.fileName,
    };
  }

  throw new Error("No file selected");
}
